.filter-year-container {
  .year-overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 1;
  }

  .filter-year {
    position: relative;
    box-sizing: border-box !important;
    height: 30px;
    width: 200px;
    background-color: white;
    border: 1px solid grey;
    border-radius: 6px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 2px 10px 10px;
    z-index: 2;

    .label {
      font-size: 16px;
      font-weight: 500;
    }

    .button {
      height: 25px;
      width: 30px;
      background-color: #f5f4f4;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      cursor: pointer;

      &:hover {
        background-color: #d0d0d0;
      }

      i {
        color: black;
      }
    }

    .content {
      position: absolute;
      display: flex;
      flex-direction: column;
      gap: 4px;
      top: 34px;
      right: 0;
      width: 200px;
      background-color: #d0d0d0;
      border-radius: 6px;
      padding: 4px;
      box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.55);

      .item {
        width: 100%;
        background-color: white;
        border-radius: 4px;
        cursor: pointer;
        border: 1px solid white;
        transition: all 300ms ease;
        padding: 4px 8px;

        &:hover {
          border: 1px solid #7460ee;
          font-weight: 500;
        }
      }
    }
  }
}
